  left: 20px;
}

#funnyCupcake-container.funnyCupcake-top-right {
  top: 20px;
  right: 20px;
}

#funnyCupcake-container.funnyCupcake-bottom-left {
  bottom: 20px;
  left: 20px;
}

#funnyCupcake-container.funnyCupcake-bottom-right {
  right: 20px;
  bottom: 20px;
}

#funnyCupcake-container .funnyCupcake {
  position: relative;
  display: flex;
  min-width: 200px;
  min-height: 60px;
}

